"use client";

import { Textarea } from "~/components/ui/textarea";
import { Switch } from "~/components/ui/switch";
import { useState } from "react";
import { useDarkMode } from "~/hooks/useDarkMode";
import { Button } from "~/components/ui/button";

export default function Home() {
  const { isDark, toggleTheme } = useDarkMode();
  const [text, setText] = useState("");
  const [response, setResponse] = useState("");
  const [isLoading, setIsLoading] = useState(false);

  const handleSubmit = async () => {
    if (!text.trim()) return;

    try {
      setIsLoading(true);
      const res = await fetch("/api/summary", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ text }),
      });

      const data = await res.json();
      setResponse(data.message);
    } catch (error) {
      console.error("Error:", error);
      setResponse("Ocurrió un error al procesar tu solicitud.");
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <main className="min-h-screen bg-white dark:bg-slate-950 transition-colors duration-300">
      {/* Switch de tema en la esquina superior derecha */}

      {/* Layout responsive: columnas en desktop grande (>1580px), filas en mobile y tablet */}
      <div className="w-full grid grid-cols-1 2xl:grid-cols-2">
        {/* Columna izquierda - Input */}
        <div className="w-full flex justify-center items-center flex-col min-h-screen p-4 sm:p-8">
          <h1 className="text-4xl sm:text-6xl font-bold tracking-tighter mb-2 sm:mb-4 mt-16 2xl:mt-0">
            Resumenes
          </h1>
          <p className="mt-2 text-gray-500 text-xl sm:text-2xl dark:text-gray-400 mb-4 sm:mb-8 text-center">
            Escribe tus ideas y resúmenes aquí
          </p>

          <div className="w-full 2xl:w-3/4 p-4 sm:p-6 2xl:p-8 flex flex-col">
            <Textarea
              placeholder="Escribe tu resumen aquí..."
              className="min-h-[250px] sm:min-h-[400px] p-4 sm:p-6 text-lg sm:text-xl w-full flex-grow"
              value={text}
              onChange={(e) => setText(e.target.value)}
              disabled={isLoading}
            />
            <Button
              onClick={handleSubmit}
              className="w-full mt-4 sm:mt-6 py-4 sm:py-6 text-base sm:text-lg"
              disabled={isLoading}
            >
              {isLoading ? "Procesando..." : "Enviar"}
            </Button>
          </div>
        </div>

        {/* Columna derecha - Resumen */}
        <div className="w-full py-10 sm:py-20 bg-slate-50 dark:bg-slate-900 rounded-lg min-h-[50vh] 2xl:min-h-screen">
          {response ? (
            <div className="h-full p-4 sm:p-6">
              <h3 className="font-medium text-lg sm:text-xl mb-3 sm:mb-4 text-slate-800 dark:text-slate-200">
                Resumen:
              </h3>
              <p className="whitespace-pre-line text-slate-800 dark:text-slate-200">
                {response}
              </p>
            </div>
          ) : (
            <div className="flex items-center justify-center h-full text-slate-400 p-4 sm:p-6">
              <p>El resumen aparecerá aquí</p>
            </div>
          )}
        </div>
      </div>
    </main>
  );
}
